export default {
	data() {
		return {
			item: '',
			ruleForm: {
				id: '',
				commodityZongJiage: ''
			},
			loading: false
		}
	},
	onLoad(option) {
		this.item = JSON.parse(decodeURIComponent(option.item));
	},

	methods: {
		baojia(id) {
			if (this.ruleForm.commodityZongJiage === '') {
				uni.showToast({
					title: "金额不能为空",
					icon: "none"
				})
				return false;
			}

			let commodityZongJiage = parseInt(this.ruleForm.commodityZongJiage);
			if (!Number.isInteger(commodityZongJiage)) {
				uni.showToast({
					title: "金额只能是数字",
					icon: "none"
				})
				return false;
			}
			if (commodityZongJiage <= 0 || commodityZongJiage > 5000) {
				uni.showToast({
					title: "金额只能在1-5000之间",
					icon: "none"
				})
				return false;
			}
			this.loading = true;

			this.ruleForm.id = id;
			//baojia
			this.$http.post(this.$urlconfig.createjiedan, this.ruleForm, {}).then(data => {
				this.loading = false;
				//{status: 1, msg: "不能重复报价", data: null, success: false}
				if (data.status === 0) {
					uni.showModal({
						title: '报价成功',
						content: '请等待商户选择，可在已报价订单中查询，'
						+'冻结金额'+data.data.dongjieAmount+'(元)，'
						+'剩余金额'+data.data.shengyuAmount+'(元)',
						success: function(res) {
							// if (res.confirm) {
							//             console.log('用户点击确定');
							//         } else if (res.cancel) {
							//             console.log('用户点击取消');
							//         }
							//返回接单页
							uni.navigateBack({
								delta:1
							});
						}
					});
				} else {
					uni.showToast({
						title: data.msg,
						icon: "none"
					})
				}

			});





		}
	}
}
